@import "../../app/variables.scss";
@import "../../app/mixins.scss";

.toggle-button {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 24px;
  width: 30px;
  background: transparent;
  border: none;
  padding: 0;
  box-sizing: border-box;
  cursor: pointer;

  &:focus {
    outline: none;
  }
}

.toggle-button__line {
  width: 30px;
  height: 2px;
  background: #f2f2f2;
}
