@import "../../app/variables.scss";
@import "../../app/mixins.scss";

.backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 100;
  top: 0;
  left: 0;
}

@media screen and (min-width: $display-tablet) {
  .backdrop {
    display: none;
  }
}
